svg {
  pointer-events: none;
}

/* Demo 1 */
.demo-drawings figcaption {
  margin: 0 auto 0 auto;
  max-width: 40em;
  width: 95%;
  text-align: center;
}

.demo-drawings figure:first-child figcaption {
  margin-top: 8em;
}

.demo-drawings figcaption h2 {
  font-weight: 300;
  font-size: 3em;
  margin-bottom: 0.2em;
}

.demo-drawings figcaption p {
  font-size: 1.3em;
}

.drawings {
  position: relative;
  max-height: 600px;
}

.illustration {
  position: absolute;
  top: 50%;
  left: 50%;
  max-height: 100%;
  max-width: 100%;
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.show {
  opacity: 1;
}

.hide {
  opacity: 0;
}

.line-drawing,
.illustration {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.line-drawing path {
  fill: none;
  stroke: white;
  stroke-width: 3;
}

path.line-round {
  stroke-linecap: round;
}

path.stroke-medium {
  stroke-width: 2;
}

path.stroke-thin {
  stroke-width: 1;
}

/* Demo 2 and demo 3 */
.demo-loading .show,
.demo-loading .hide {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.codrops-logo {
  position: relative;
  margin: 0 auto 30px;
  width: 80px;
  height: 80px;
  background: url(../img/codrops_logo.png) no-repeat 50% 50%;
}

.demo-loading .headline {
  position: relative;
  color: #2173a3;
  text-align: center;
}

.demo-loading .headline div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #5ca9d6;
}

.demo-loading .headline h1 {
  margin: 0;
  padding: 3em 0;
  font-size: 3em;
  position: relative;
  z-index: 10;
}

.demo-loading .headline h1 span:last-child {
  display: block;
  font-weight: 300;
  font-size: 50%;
}

.demo-loading section {
  margin: 2em auto 8em;
  max-width: 700px;
  text-align: center;
}

.demo-loading section figure {
