/* Animate scroll-down*/
.scroll-down {
  position: absolute;
  bottom: 2%;
  left: 50%;
  transform: translate(-50%, 0);
  background: #4e5559 linear-gradient(transparent 0%, transparent 50%, #d0021b 50%, #d0021b 100%);
  width: 30px;
  height: 52px;
  background-size: 100% 100%;
  border-radius: 100px;
  background-size: 225%;
  animation: colorSlide 5s linear infinite, nudgeMouse 5s ease-out infinite;
}

.scroll-down:before, .scroll-down:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.scroll-down:before {
  width: 24px;
  height: 46px;
  background-color: #ffffff;
  border-radius: 100px;
}

.scroll-down:after {
  background-color: #d0021b;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  animation: trackBallSlide 5s linear infinite;
}

@keyframes colorSlide {
  0% {
    background-position: 0% 100%;
  }
  20% {
    background-position: 0% 0%;
  }
  21% {
    background-color: #4e5559;
  }
  29.99% {
    background-color: #d0021b;
    background-position: 0% 0%;
  }
  30% {
    background-color: #4e5559;
    background-position: 0% 100%;
  }
  50% {
    background-position: 0% 0%;
  }
  51% {
    background-color: #4e5559;
  }
  59% {
    background-color: #d0021b;
    background-position: 0% 0%;
  }
  60% {
    background-color: #4e5559;
    background-position: 0% 100%;
  }
  80% {
    background-position: 0% 0%;
  }
  81% {
    background-color: #4e5559;
  }
  90%, 100% {
    background-color: #d0021b;
  }
}

@keyframes trackBallSlide {
  0% {
    opacity: 1;
    transform: scale(1) translateY(-16px);
  }
  6% {
    opacity: 1;
    transform: scale(0.9) translateY(4px);
  }
  14% {
    opacity: 0;
    transform: scale(0.4) translateY(32px);
  }
  15%, 19% {
    opacity: 0;
    transform: scale(0.4) translateY(-16px);
  }
  28%, 29.99% {
    opacity: 1;
    transform: scale(1) translateY(-16px);
  }
  30% {
    opacity: 1;
    transform: scale(1) translateY(-16px);
  }
  36% {
    opacity: 1;
    transform: scale(0.9) translateY(4px);
  }
  44% {
    opacity: 0;
    transform: scale(0.4) translateY(32px);
  }
  45%, 49% {
    opacity: 0;
    transform: scale(0.4) translateY(-16px);
  }
  58%, 59.99% {
    opacity: 1;
    transform: scale(1) translateY(-16px);
  }
  60% {
    opacity: 1;
    transform: scale(1) translateY(-16px);
  }
  66% {
    opacity: 1;
    transform: scale(0.9) translateY(4px);
  }
  74% {
    opacity: 0;
    transform: scale(0.4) translateY(32px);
  }
  75%, 79% {
    opacity: 0;
    transform: scale(0.4) translateY(-16px);
  }
  88%, 100% {
    opacity: 1;
    transform: scale(1) translateY(-16px);
  }
}

@keyframes nudgeMouse {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(8px);
  }
  30% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(8px);
  }
  60% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(8px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes nudgeText {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(2px);
  }
  30% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(2px);
  }
  60% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(2px);
  }
  90% {
    transform: translateY(0);
  }
}

@keyframes colorText {
  21% {
    color: #4e5559;
  }
  30% {
    color: #d0021b;
  }
  51% {
    color: #4e5559;
  }
  60% {
    color: #d0021b;
  }
  81% {
    color: #4e5559;
  }
  90% {
    color: #d0021b;
  }
}

/* end scroll-down */
.line-drawing path {
  fill: transparent;
}

.line-drawing .circle-show {
  transition: all 1.4s ease;
}

.line-drawing .circle-show.top {
  animation: circleShowTop 5s linear 1;
  animation-timing-function: ease-in;
}

.line-drawing.show path {
  transition: fill .4s ease;
  fill: #fff;
}

.line-drawing.show .path-show {
  transition: fill .4s ease;
  stroke: none;
}

.line-drawing .path-dasharray {
  stroke-dasharray: 1, 3 !important;
  stroke: #646464 !important;
  stroke-width: 2 !important;
}

@keyframes circleShowTop {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1 !important;
  }
}

